import { BancoService } from './../../../services/banco.service';
import { QuillModule, QuillEditorComponent } from 'ngx-quill';
import { Platform, AlertController, LoadingController, ToastController } from '@ionic/angular';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import * as Quill from 'quill'
// import { Quill } from 'quill'
import { debounceTime, distinctUntilChanged } from 'rxjs/operators'
import { ApiService } from 'src/app/services/api.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-nova-atividade',
    templateUrl: './nova-atividade.page.html',
    styleUrls: ['./nova-atividade.page.scss'],
})
export class NovaAtividadePage implements OnInit {
    // @ViewChild('editor', {
    //     static: true
    // }) editor: QuillEditorComponent

    public atividadeForm: FormGroup
    public imagens = []
    public editor
    private usuario
    constructor(
        public formBuilder: FormBuilder,
        public platform: Platform,
        public alertCtrl: AlertController,
        public loadingCtrl: LoadingController,
        public toastCtrl: ToastController,
        public router: Router,
        public banco: BancoService,
        private api: ApiService
    ) {
        this.atividadeForm = formBuilder.group({
            titulo: ['', Validators.compose([Validators.required, Validators.minLength(10)])],
            autor: ['', Validators.compose([Validators.required, Validators.minLength(6)])],
            descricao: ['']
        })
        // q('a')
        // console.log("editor: ", editor);
        // var editor = new Quill('#editor', {
        //     modules: { toolbar: '#toolbar' },
        //     theme: 'snow'
        // });

    }

    async ngOnInit() {
        let toolbarOptions = [
            [{ 'font': [] }],
            ['bold', 'italic', 'underline', 'strike'],        // toggled buttons

            [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme

            [{ 'list': 'ordered'}, { 'list': 'bullet' }],
            [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
            [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
          
            [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
            [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
          
            [{ 'align': [] }],
          
            ['clean']  
        ]
        this.editor = new Quill('#editor', {
            theme: 'snow', modules: {
                toolbar: toolbarOptions
            }
        })

        this.usuario = await this.banco.get('user-credentials')
        console.log("usuario: ", this.usuario);
        
        // this.editor
        //     .onContentChanged
        //     .pipe(
        //         debounceTime(400),
        //         distinctUntilChanged()
        //     )
        //     .subscribe((data) => {
        //         // tslint:disable-next-line:no-console
        //         console.log('view child + directly subscription', data)
        //     })
    }

    selecionarImagens() {
        if (this.platform.is('capacitor')) {
            this.abreImagemCapacitor()
        } else {
            var target = document.getElementById('imagePicker')
            target.click()
        }
    }

    abreImagemCapacitor() { }

    abreArquivo(e) {
        var file = e.target.files[0];
        if (!file) { return; }
        var reader = new FileReader();
        reader.onload = (e) => {
            var image = new Image();
        }
    }

    abreArquivos(e) {
        var files = e.target.files;
        if (!files.length) { return; }
        for (let file of files) {
            console.log("imagem");

            var reader = new FileReader();
            reader.onload = (e) => {
                // this.imagemDestaque = e.target['result'];
                // console.log('this.imagemDestaque: ', this.imagemDestaque);

                var image: any = new Image();
                image.src = e.target['result'];
                image.onload = async () => {
                    let imageReturn = await this.comprimir(e.target['result'])
                    let imagem = {
                        baixa_resolucao: imageReturn
                    }
                    console.log("imagem: ", imagem);

                    this.imagens.push(imagem)
                }
            };
            reader.readAsDataURL(file);
        }
    }

    comprimir(img?) {
        return new Promise<any>(done => {
            let imagemOtimizada = null;
            var image = new Image();
            image.src = img

            image.onload = () => {
                var canvas = document.createElement('canvas'),
                    ctx = canvas.getContext('2d');
                canvas.width = image.naturalWidth;
                canvas.height = image.naturalHeight;

                if (canvas.width > 1080 && canvas.width > canvas.height) {
                    console.log('width bigger:', canvas.width, canvas.height);

                    let proporcao = (((1080 * 100) / canvas.width)) / 100;
                    canvas.width = Math.round(image.naturalWidth * proporcao);
                    canvas.height = Math.round(image.naturalHeight * proporcao);

                } else if (canvas.height > 1080 && canvas.height > canvas.width) {
                    console.log('height bigger:', canvas.height, canvas.width);
                    let proporcao = (((1080 * 100) / canvas.height)) / 100;
                    canvas.height = Math.round(image.naturalHeight * proporcao);
                    canvas.width = Math.round(image.naturalWidth * proporcao);
                }

                console.log('width/height:', canvas.width, canvas.height);
                ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
                imagemOtimizada = canvas.toDataURL("image/jpeg", 0.3);
                done(imagemOtimizada);
            };
        });
    }

    async removerImagem(index) {
        let alert = await this.alertCtrl.create({
            subHeader: "Deseja remover esta imagem?", buttons: [{
                text: "Não"
            }, {
                text: "Sim",
                handler: () => {
                    this.imagens.splice(index, 1);
                    console.log('imagens', this.imagens);
                }
            }]
        })
        alert.present()
    }

    changedEditor(e) {
        // console.log("changed", e);
        // console.log("texto", e.content());

    }

    async salvar() {
        // console.log(JSON.stringify(this.editor.getContents()));
        // return;
        let loading = await this.loadingCtrl.create({ message: "Salvando..." })
        loading.present()
        let body = `
            mutation {
                novaAtividade(
                    id_usuario: ${this.usuario['id']}
                    titulo: "${this.atividadeForm.value.titulo}",
                    autor: "${this.atividadeForm.value.autor}",
                    descricao: "${this.atividadeForm.value.descricao}",
                    texto: ${JSON.stringify(JSON.stringify(this.editor.getContents()))},
                    imagens: ${JSON.stringify(this.imagens).replace(/\"baixa_resolucao\"/g, 'baixa_resolucao')}
                ){
                    id
                }
            }
        `
        let retorno = await this.api.post({ query: body });
        loading.dismiss()

        if (retorno.data && !retorno.errors) {
            let toast = await this.toastCtrl.create({ message: "Cadastro realizado com sucesso", duration: 3000 })
            toast.present()
            this.router.navigate(['/atividades/minhas-atividades'])
        } else {
            let alerta = await this.alertCtrl.create({ header: "Ocorreu um erro", message: "Tente novamente, ou contate o suporte." })
            alerta.present()
        }
    }

}
